import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'filter',
    pure: false
})
export class FilterPipe implements PipeTransform {
    transform(items: any[], term): any {
        return items.filter(item => item.title.toLowerCase().includes(term.toLowerCase()));
    }
}

