import { Component, OnInit, ViewEncapsulation } from "@angular/core";
import { NgForm } from "@angular/forms";
import { AppService} from '../app.service';

@Component({
  templateUrl: "./contact.component.html",
  styleUrls: ["./contact.component.css"],
  encapsulation: ViewEncapsulation.None
})
export class ContactComponent implements OnInit {
  submitted: boolean = false;
  thanks: boolean;
  constructor(private appService: AppService) {}

  ngOnInit() {}

  onSubmit(form: NgForm) {
    if (form.valid) {
      let googleDocsUrl = "https://docs.google.com/forms/d/e/1FAIpQLSe8huDih07_owHAGLCwQ7Ql78PkaS2hT5wjnT1wg2MhVzae2w/formResponse?"
      let name = form.value.userName;
      let email = form.value.email;
      let comment = form.value.comment;
      let URL = googleDocsUrl + "entry.2005620554=" + name + "&entry.1045781291=" + email + "&entry.839337160=" + comment;
      this.appService.contactusSubmit(URL).subscribe();
      this.submitted = true;
      this.thanks = true;
    }
  }
  
}


